///// Component /////
interface ComponentPayload {
  tagName? : string
  props? : {
    [key : string] : unknown
  }
  state? : {
    [key : string] : unknown
  }
}
export class Component {
  public el
  public props
  public state
    constructor(payload : ComponentPayload= {}) {
      const {
        tagName = 'div', // ÏµúÏÉÅÏúÑ ÏöîÏÜåÏùò ÌÉúÍ∑∏ Ïù¥Î¶Ñ
        props = {},
        state = {}
      } = payload
      this.el = document.createElement(tagName) // Ïª¥Ìè¨ÎÑåÌä∏Ïùò ÏµúÏÉÅÏúÑ ÏöîÏÜå
      this.props = props // Ïª¥Ìè¨ÎÑåÌä∏Í∞Ä ÏÇ¨Ïö©Îê† Îïå Î∂ÄÎ™® Ïª¥Ìè¨ÎÑåÌä∏ÏóêÏÑú Î∞õÎäî Îç∞Ïù¥ÌÑ∞
      this.state = state // Ïª¥Ìè¨ÎÑåÌä∏ ÏïàÏóêÏÑú ÏÇ¨Ïö©Ìï† Îç∞Ïù¥ÌÑ∞
      this.render()
    }
    render() { // Ïª¥Ìè¨ÎÑåÌä∏Î•º Î†åÎçîÎßÅÌïòÎäî Ìï®Ïàò
      // ...
    }
  }
  
  
  ///// Router /////
  interface Route {
    path : string,
    component : typeof Component
  }

  type Routes = Route[] //ÌÉÄÏûÖ Î≥ÑÏπ≠ ÏÉùÏÑ±
  // ÌéòÏù¥ÏßÄ Î†åÎçîÎßÅ!
  function routeRender(routes : Routes) {
    // Ï†ëÏÜçÌï† Îïå Ìï¥Ïãú Î™®ÎìúÍ∞Ä ÏïÑÎãàÎ©¥(Ìï¥ÏãúÍ∞Ä ÏóÜÏúºÎ©¥) /#/Î°ú Î¶¨Îã§Ïù¥Î†âÌä∏!
    if (!location.hash) {
      history.replaceState(null, '', '/#/') // (ÏÉÅÌÉú, Ï†úÎ™©, Ï£ºÏÜå)
    }
    const routerView = document.querySelector('router-view')
    const [hash, queryString = ''] = location.hash.split('?') // Î¨ºÏùåÌëúÎ•º Í∏∞Ï§ÄÏúºÎ°ú Ìï¥Ïãú Ï†ïÎ≥¥ÏôÄ ÏøºÎ¶¨Ïä§Ìä∏ÎßÅÏùÑ Íµ¨Î∂Ñ
  
    // 1) ÏøºÎ¶¨Ïä§Ìä∏ÎßÅÏùÑ Í∞ùÏ≤¥Î°ú Î≥ÄÌôòÌï¥ ÌûàÏä§ÌÜ†Î¶¨Ïùò ÏÉÅÌÉúÏóê Ï†ÄÏû•!
    interface Query {
      [key : string] : string
    }
    const query = queryString
      .split('&')
      .reduce((acc, cur) => {
        const [key, value] = cur.split('=')
        acc[key] = value
        return acc
      }, {} as Query) //ÌÉÄÏûÖ Îã®Ïñ∏
    history.replaceState(query, '') // (ÏÉÅÌÉú, Ï†úÎ™©)
  
    // 2) ÌòÑÏû¨ ÎùºÏö∞Ìä∏ Ï†ïÎ≥¥Î•º Ï∞æÏïÑÏÑú Î†åÎçîÎßÅ!
    const currentRoute = routes.find(route => new RegExp(`${route.path}/?$`).test(hash))
    if(routerView) {
      routerView.innerHTML = ''
      currentRoute && routerView.append(new currentRoute.component().el) //currentRouteÍ∞Ä Ï∞∏Ïù¥Î©¥ Îí§Ïóê ÏΩîÎìúÎ•º Ïã§ÌñâÌïòÍ≤†Îã§

    }
    
  
    // 3) ÌôîÎ©¥ Ï∂úÎ†• ÌõÑ Ïä§ÌÅ¨Î°§ ÏúÑÏπò Î≥µÍµ¨!
    window.scrollTo(0, 0)
  }
  export function createRouter(routes : Routes) {
    // ÏõêÌïòÎäî(ÌïÑÏöîÌïú) Í≥≥ÏóêÏÑú Ìò∏Ï∂úÌï† Ïàò ÏûàÎèÑÎ°ù Ìï®Ïàò Îç∞Ïù¥ÌÑ∞Î•º Î∞òÌôò!
    return function () {
      window.addEventListener('popstate', () => {
        routeRender(routes)
      })
      routeRender(routes)
    }
  }
  
  
  ///// Store /////
interface StoreObservers {
  [key:string] : SubscribeCallback[]
 }
interface SubscribeCallback {
  (arg : unknown) : void
}
  export class Store<S> {
    public state= {} as S
    private observers = {} as StoreObservers

    constructor(state : S) {
      for (const key in state) {
        // Í∞Å ÏÉÅÌÉúÏóê ÎåÄÌïú Î≥ÄÍ≤Ω Í∞êÏãú(Setter) ÏÑ§Ï†ï!
        Object.defineProperty(this.state, key, {
          // Getter
          get: () => state[key],
          // Setter
          set: val => {
            state[key] = val
            if (Array.isArray(this.observers[key])) { // Ìò∏Ï∂úÌï† ÏΩúÎ∞±Ïù¥ ÏûàÎäî Í≤ΩÏö∞!
              this.observers[key].forEach(observer => observer(val))
            }
          }
        })
      }
    }
    // ÏÉÅÌÉú Î≥ÄÍ≤Ω Íµ¨ÎèÖ!
    subscribe(key : string, cb : SubscribeCallback) {
      Array.isArray(this.observers[key]) // Ïù¥ÎØ∏ Îì±Î°ùÎêú ÏΩúÎ∞±Ïù¥ ÏûàÎäîÏßÄ ÌôïÏù∏!
        ? this.observers[key].push(cb) // ÏûàÏúºÎ©¥ ÏÉàÎ°úÏö¥ ÏΩúÎ∞± Î∞ÄÏñ¥ÎÑ£Í∏∞!
        : this.observers[key] = [cb] // ÏóÜÏúºÎ©¥ ÏΩúÎ∞± Î∞∞Ïó¥Î°ú Ìï†Îãπ!
  
      // ÏòàÏãú)
      // observers = {
      //   Íµ¨ÎèÖÌï†ÏÉÅÌÉúÏù¥Î¶Ñ: [Ïã§ÌñâÌï†ÏΩúÎ∞±1, Ïã§ÌñâÌï†ÏΩúÎ∞±2]
      //   movies: [cb, cb, cb],
      //   message: [cb]
      // }
    }
  }